.Progress {
	display: flex;
	flex-direction: column;
	flex-shrink: 0;
	align-self: stretch;
	padding: 1rem 0;

	&:not(.playing) {
		progress::-webkit-progress-bar {
			background-color: rgba(0, 0, 0, 0.1);
		}
	}

	progress {
		width: 100%;
		appearance: none;
		-webkit-appearance: none;
		height: 0.25rem;

		&::-webkit-progress-bar {
			border-radius: 1rem;
			background-color: rgba(0, 0, 0, 0.3);
		}

		&::-webkit-progress-value {
			border-radius: 1rem;
			background-color: #0f0;
		}
	}
}
