.Controls {
	display: flex;
	flex-direction: column;
	flex-shrink: 0;
	align-items: center;
	align-self: stretch;
	justify-content: center;
	padding: 0.5rem 1rem 1rem 1rem;

	.ControlsSet {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;

		&:last-child {
			align-self: stretch;
		}

		.Control {
			margin-left: 2rem;

			&:first-child {
				margin-left: 0;
			}
		}
	}
}
