body {
	background: #ffffff;
	font-family: "Roboto", Arial, sans-serif;
	font-size: 15px;
	line-height: 20px;
	margin: 0;
	padding: 0;
}

div,
p,
img {
	padding: 0;
	margin: 0;
}

div {
	box-sizing: border-box;
}

button,
input[type="password"],
input[type="text"],
textarea,
select {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: inherit;
	box-sizing: border-box;
}

a {
	color: inherit;
	text-decoration: none;

	&:hover {
		text-decoration: underline;
	}
}
